.BlockControls {

    display: flex
}

.BlockControl {

    display: flex;

    align-items: center;

    gap: 0.5rem;

    --tw-border-opacity: 1;

    border-color: rgb(167 167 167 / var(--tw-border-opacity));

    padding-left: 0.5rem;

    padding-right: 0.5rem;

    letter-spacing: 0.05em;

    --tw-text-opacity: 1;

    color: rgb(34 34 34 / var(--tw-text-opacity))
}

@media (min-width: 768px) {

    .BlockControl {

        padding-left: 1.5rem;

        padding-right: 1.5rem
    }
}

.BlockControl--danger {

    --tw-text-opacity: 1;

    color: rgb(209 0 0 / var(--tw-text-opacity))
}

.BlockControl--left {

    border-top-width: 1px;

    border-bottom-width: 1px;

    border-left-width: 1px
}

.BlockControl--right {

    border-top-width: 1px;

    border-bottom-width: 1px;

    border-right-width: 1px
}

.BlockControl--center {

    border-width: 1px
}

.BlockControl--drag {

    border-top-width: 1px;

    border-bottom-width: 1px;

    border-left-width: 1px;

    padding-left: 0.25rem;

    padding-right: 0.25rem
}

@media (min-width: 768px) {

    .BlockControl--drag {

        padding-left: 0.75rem;

        padding-right: 0.75rem
    }
}

.BlockControl__Label {

    font-size: 0.75rem;

    line-height: 1rem;

    font-weight: 600;

    text-transform: uppercase
}

.NestedBlockControl {

    padding-left: 0.5rem;

    padding-right: 0.5rem;

    --tw-text-opacity: 1;

    color: rgb(34 34 34 / var(--tw-text-opacity))
}

@media (min-width: 640px) {

    .NestedBlockControl {

        padding-left: 0.75rem;

        padding-right: 0.75rem
    }
}