.BlocksTable {
          width: 100%;
          border-radius: 0.375rem;
          text-align: left
}

  .BlocksTable thead {
          font-size: 0.75rem;
          line-height: 1rem;
          text-transform: uppercase;
          letter-spacing: 0.05em;
          --tw-text-opacity: 1;
          color: rgb(120 120 120 / var(--tw-text-opacity))
}

  .BlocksTable tr {
          border-bottom-width: 1px;
          border-bottom-color: rgb(167 167 167 / var(--tw-border-opacity));
          --tw-border-opacity: 0.5
}

  .BlocksTable tr:last-child {
          border-bottom-width: 0px
}

  .BlocksTable th {
          padding: 1rem;
          font-weight: 400
}

  .BlocksTable th:last-child {
          padding-right: 0px
}

  .BlocksTable td {
          padding: 1rem
}

  .BlocksTable td:last-child {
          padding-right: 0px
}

  .BlocksTable__Row__Title {
          font-weight: 700;
          --tw-text-opacity: 1;
          color: rgb(51 51 51 / var(--tw-text-opacity))
}

  .BlocksTable__Row__LinkedContent__Wrapper {
          display: flex;
          align-items: center;
          gap: 0.25rem;
          font-size: 0.875rem;
          line-height: 1.25rem;
          --tw-text-opacity: 1;
          color: rgb(120 120 120 / var(--tw-text-opacity))
}

  .BlocksTable__Row__LinkedContent__Wrapper > button {
          text-decoration-line: underline
}

  .BlocksTable__Row__Langs__Wrapper {
          display: flex;
          align-items: center;
          gap: 0.75rem
}

  .BlocksTable__Row__Langs__Wrapper > span {
          border-width: 1px;
          --tw-border-opacity: 1;
          border-color: rgb(68 68 68 / var(--tw-border-opacity));
          --tw-bg-opacity: 1;
          background-color: rgb(245 245 245 / var(--tw-bg-opacity));
          padding-top: 0.25rem;
          padding-bottom: 0.25rem;
          padding-left: 0.75rem;
          padding-right: 0.75rem;
          font-weight: 600;
          border-radius: 90px
}

  .BlocksTable__Row__Actions__Wrapper {
          display: flex;
          gap: 2rem
}